<template>
    <div v-if="!ready" class="FirstConfirm">
        <p>主人欢迎回来喵，是要开始学习了喵</p>
        <div style="text-align: center;">
            <button @click="confirmReady()">我准备好了</button>
        </div>
    </div>
    <div v-if="ready&&!paused" class="MainPage">
        <p>现在开始记时喵~</p>
        <p>当前时间是{{ nowHour }}时{{ nowMin }}分{{ nowSec }}秒喵~</p>
        <p>{{ nowPlan }}</p>
        <p>主人这次一共学习了{{ hr }}小时{{ min }}分钟{{ sec }}秒喵,主人好棒喵,最喜欢主人了喵~</p>
        <p>主人这次的学习时间是{{ phr }}小时{{ pmin }}分钟{{ psec }}秒喵~</p>
        <div style="text-align: center;">
            <button @click="confirmPaused()">我想休息一下</button>
        </div>
    </div>
    <div v-if="paused" class="PausePage">
        <p>主人休息一下也是很重要的喵,请好好休息喵~</p>
        <p>已经休息了{{ phr }}小时{{ pmin }}分钟{{ psec }}秒喵~</p>
        <div style="text-align: center;">
            <button @click="paused=false;">我休息好了，继续学习</button>
        </div>
    </div>
</template>
<script setup>
    import { ref } from 'vue';
    var hr = ref(0);//学习时间记录
    var min = ref(0);
    var sec = ref(0);
    var phr = ref(0);//暂停时的时间记录
    var pmin = ref(0);
    var psec = ref(0);
    var ready = ref(false);
    var paused = ref(false);

    var nowHour = ref(0);//当前时间记录
    var nowMin = ref(0);
    var nowSec = ref(0);

    var nowPlan = ref("");//当前时间对应的计划字符串

    function confirmReady() {//确认准备好了
        ready.value = true;
        hr.value = 0;
        min.value = 0;
        sec.value = 0;
        phr.value = 0;
        pmin.value = 0;
        psec.value = 0;
    }
    function confirmPaused() {//确认暂停
        paused.value = true;
        phr.value = 0;
        pmin.value = 0;
        psec.value = 0;
    }
    setInterval(() => {
        getNewTime();//计时器自动增时
        getNowTime();//获取当前时间
        nowPlanCheck();//检查当前时间按照计划更改显示字符串
    }, 1000);
    function getNewTime() {
        if (!paused.value){
            sec.value += 1;
            if (sec.value >= 60) {
                sec.value = 0;
                min.value += 1;
            }
            if (min.value >= 60) {
                min.value = 0;
                hr.value += 1;
            }
        }
        psec.value += 1;
        if (psec.value >= 60) {
            psec.value = 0;
            pmin.value += 1;
        }
        if (pmin.value >= 60) {
            pmin.value = 0;
            phr.value += 1;
        }
    }
    function getNowTime() {
        nowHour.value = new Date().getHours();
        nowMin.value = new Date().getMinutes();
        nowSec.value = new Date().getSeconds();
    }
    function nowPlanCheck() {
        if (nowHour.value >= 6 && nowHour.value < 9) {
            nowPlan.value = "早上好喵，主人，今天也好早就来看人家了喵，好好学习也不要忘记吃早饭喵~";
        } else if ((nowHour.value >= 11 && nowMin.value > 40) && nowHour.value < 14) {
            nowPlan.value = "中午好喵，主人，已经是休息的时间了，记得吃午饭喵~";
        } else if (nowHour.value >= 14 && (nowHour.value <= 17 && nowMin.value < 30)) {
            nowPlan.value = "下午好喵，主人，今天也要好好加油喵~";
        } else if ((nowHour.value >= 17 && nowMin.value >= 30) && nowHour.value < 22) {
            nowPlan.value = "时间好快呀，晚上好喵，主人，要吃好多好吃的喵~";
        } else {
            nowPlan.value = "夜深了喵，勤快的主人最喜欢了喵，主人要早点休息喵~";
        }
    }
</script>
